<template>
    <div>
        <basic-header :items="['系统管理','轮播管理']"></basic-header>
        <el-card>
            <el-tabs v-model="activeName">
                <el-tab-pane label="轮播查看" name="list">
                    <el-table :data="tableData">
                        <el-table-column label="id" prop="id" width="100"></el-table-column>
                        <el-table-column label="图片" prop="url" align="center">
                            <template #default="o">
                                <img :src="o.row.url" alt="" width="100" height="50">
                            </template>
                        </el-table-column>
                        <el-table-column label="排序" prop="sort" align="center"></el-table-column>
                        <el-table-column label="操作">
                            <template #default="o">
                                <el-button type="text" @click="edit(o.row)">编辑</el-button>
                                <el-button type="text" @click="del(o.row.id)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="轮播添加" name="add">
                    <el-row>
                        <el-col :span="12">
                            <el-form :model="submitForm" :rules="rules" ref="submitForm" label-width="80px">
                                <el-form-item label="排序" prop="sort">
                                    <el-input v-model="submitForm.sort"></el-input>
                                </el-form-item>
                                <el-form-item label="商品id" prop="gid">
                                    <el-input v-model="submitForm.gid"></el-input>
                                </el-form-item>
                                <el-form-item label="图片" prop="url">
                                    <el-upload
                                            class="avatar-uploader"
                                            :action="uploadUrl"
                                            :show-file-list="false"
                                            :on-success="handleAvatarSuccess"
                                            :before-upload="beforeAvatarUpload">
                                        <img v-if="submitForm.url" :src="submitForm.url" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="submit">提交</el-button>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="50%"
        >
            <el-form :model="updateForm" :rules="rules" ref="updateForm" label-width="80px">
                <el-form-item label="排序" prop="sort">
                    <el-input v-model="updateForm.sort"></el-input>
                </el-form-item>
                <el-form-item label="商品id" prop="gid">
                    <el-input v-model="updateForm.gid"></el-input>
                </el-form-item>
                <el-form-item label="图片" prop="url">
                    <el-upload
                            class="avatar-uploader"
                            :action="uploadUrl"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess1"
                            :before-upload="beforeAvatarUpload">
                        <img v-if="updateForm.url" :src="updateForm.url" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                    </el-upload>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="update">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>
<script>
    import Header from "@/components/Header.vue"
    export default {
        data(){
            return {
                activeName: "list",
                tableData: [],
                api: "/api/banners/banners",
                submitForm: {
                    url: "",
                    sort: "",
                    gid: ""
                },
                rules: {
                    url: {required: true, message: "请选择图片"},
                    sort: {required: true, message: "请输入图片排序", trigger: "blur"},
                    gid: {required: true, message: "请输入绑定商品id", trigger: "blur"}
                },
                uploadUrl: "/api/upload/upload",
                dialogVisible: false,
                updateForm: {}
            }
        },
        methods: {
            fetchData(){
                this.axios.get(this.api).then(res => {
                    if (res.data.code === 200) {
                        this.tableData=res.data.data;
                    }
                })
            },
            handleAvatarSuccess(res) {
                this.submitForm.url=res;
                this.$refs.submitForm.validateField("url");
            },
            handleAvatarSuccess1(res) {
                this.updateForm.url=res;
                this.$refs.updateForm.validateField("url");
            },
            beforeAvatarUpload(file) {
                const isJPG=file.type === 'image/jpeg';
                const isLt500k=file.size / 1024 < 500;
                if (!isJPG) {
                    this.$message.error('上传商品图片只能是 JPG 格式!');
                }
                if (!isLt500k) {
                    this.$message.error('上传商品图片大小不能超过500kb!');
                }
                return isJPG && isLt500k;
            },
            submit(){
                this.$refs.submitForm.validate(valid => {
                    if (valid) {
                        this.axios.post(this.api, this.submitForm).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("提交成功");
                                this.activeName="list";
                                this.$refs.submitForm.resetFields();
                                this.fetchData();
                            }
                        })
                    } else {
                        this.$message.error("验证失败");
                    }
                })
            },
            del(id){
                this.$confirm('此操作将永久删除该信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {
                            id
                        }
                    }).then(res => {
                        if (res.data.code === 200) {
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            edit(obj){
                this.updateForm=obj;
                this.dialogVisible=true;
            },
            update(){
                this.$refs.updateForm.validate(valid => {
                    if (valid) {
                        delete this.updateForm.create_time;
                        this.axios.put(this.api, this.updateForm).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("修改成功");
                                this.dialogVisible=false;
                                this.fetchData();
                            }
                        })
                    } else {
                        this.$message.error("验证失败");
                    }
                })
            }
        },
        components: {
            "basic-header": Header
        },
        created(){
            this.fetchData();
        },
        mounted(){

        }
    }
</script>
<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
        border: 1px dashed #d9d9d9;
    }

    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }

</style>  